<template lang="html">
<div>
  <PostHeader :author="author"></PostHeader>
  <ParagraphWithPhoto></ParagraphWithPhoto>
  <ContentParagraph></ContentParagraph>
  <ContentParagraph></ContentParagraph>
  <h4><strong>Lorem ipsum dolor sit.</strong></h4>
<ol class="list">
  <li>
    <div class="">
      <ContentParagraph></ContentParagraph>
    </div>
  </li>
  <li>
    <div class="">
      <ContentParagraph></ContentParagraph>
    </div>
  </li>
  <li>
    <div class="">
      <ContentParagraph></ContentParagraph>
    </div>
  </li>
</ol>
<br>
<img src="http://localhost:8080/public/images/default/image.png" alt="" style="width:100%;">
<p>{{body}}</p>
</div>
</template>

<script>
import PostHeader from './PostHeader.vue'
import ParagraphWithPhoto from './ParagraphWithPhoto.vue'
import ContentParagraph from './ContentParagraph.vue'
export default {
  name: 'BlogPost',
  props: ['author'],
  data() {
    return {
      body: ''
    }
  },
  components: {
    'PostHeader': PostHeader,
    'ParagraphWithPhoto': ParagraphWithPhoto,
    'ContentParagraph': ContentParagraph
  },
  created() {
    fetch('http://localhost:8080/api/defaulttext')
    .then(res => res.json())
    .then(json => {
      this.body = json.ParagraphBody
    })
  }
}
</script>

<style lang="css">
.social-links {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}

.byline {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px;
}

.social-icon {
  font-size:40px;
  margin:5px;
}
</style>



