<template lang="html">
  <div class="">
    <h4><strong>{{title}}</strong></h4>
    <p>{{body}}</p>
    <br>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: '',
      body: ''
    }
  },
  created() {
    fetch('http://localhost:8080/api/defaulttext')
    .then(res => res.json())
    .then(json => {
      this.title = json.ParagraphTitle
      this.body = json.ParagraphBody
    })
  }
}
</script>

<style lang="css">
</style>



