<template lang="html">
<div>
  <h1>Edit Post</h1>
  <button type="button" name="delete"@click="deletePost">Delete Post </button>
  <Editor mode="edit"></Editor>
</div>
</template>

<script>
import Editor from "./Editor.vue"
export default {
  data() {
    return {
      body: ''
    }
  },
  components: {
    'Editor': Editor
  },
  methods: {
    deletePost() {
      if (confirm("Are you sure you want to delete this post?")) {
        fetch(`http://localhost:8080/blogs/${this.$route.params.id}`, {
          method: 'DELETE'
        }).then(res => window.location.href = res.url)
      }
    }
  }
}
</script>

<style lang="css">
</style>



