<template lang="html">
  <div class="">
    <PostHeader :author="author"></PostHeader>
    <p>{{body}}</p>
    <p><span class="fr-video fr-fvc fr-dvb fr-draggable" contenteditable="false" draggable="true"><iframe width="640" height="360" src="https://www.youtube.com/embed/w-kN7n9lguA" frameborder="0" allowfullscreen=""></iframe></span><br></p>
    <p>{{body}}</p>
  </div>
</template>

<script>
import PostHeader from './PostHeader.vue'
export default {
  props: ['author'],
  data() {
    return {
      body: ''
    }
  },
  components: {
    'PostHeader': PostHeader
  },
  created() {
    fetch('http://localhost:8080/api/defaulttext')
    .then(res => res.json())
    .then(json => {
      this.body = json.ParagraphBody
    })
  }
}
</script>

<style lang="css">
.social-links {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}

.byline {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px;
}

.social-icon {
  font-size:40px;
  margin:5px;
}
</style>



