<template lang="html">
<div>
  <p style="display:flex">{{body}}
    <img src="http://localhost:8080/public/images/default/logo.png" alt="" style="margin:0px 10px;">
  </p>
  <br>
</div>
</template>

<script>
export default {
  data() {
    return {
      body: ''
    }
  },
  created() {
    fetch('http://localhost:8080/api/defaulttext')
    .then(res => res.json())
    .then(json => {
      this.body = json.ParagraphBody
    })
  }
}
</script>

<style lang="css">
</style>



