import Vue from 'vue'
import Router from 'vue-router'
import NewPost from './components/NewPost'
import EditPost from './components/EditPost'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/blog/new',
      name: 'NewPost',
      component: NewPost
    },
    {
      path: '/blogs/:id/edit',
      name: 'EditPost',
      component: EditPost
    }
  ]
})



